.ant-notification {
  width: 298px !important; }

.ant-notification-notice {
  padding: 12px !important; }

.ant-notification-notice-message {
  margin-bottom: 0 !important;
  padding-right: 0px !important; }

.ant-notification-notice-close {
  margin-top: -5px !important;
  top: 6px !important;
  right: 12px !important; }
  .ant-notification-notice-close svg {
    width: 10px !important;
    height: 10px !important;
    color: #001a33 !important; }

.NoticeBox {
  width: 360px;
  position: fixed;
  right: 400px;
  top: 82px;
  z-index: 900;
  box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.12); }
  .NoticeBox__Header {
    display: flex;
    align-items: center;
    background-color: #001a33;
    height: 58px;
    padding: 12px 12px;
    color: #ffffff;
    position: relative;
    cursor: pointer; }
    .NoticeBox__Header .userName {
      margin-right: auto;
      font-family: SFBoldText;
      font-size: 20px;
      font-weight: 500;
      font-stretch: normal;
      font-style: normal;
      line-height: 24px;
      letter-spacing: normal;
      color: #ffffff; }
    .NoticeBox__Header .cancel {
      align-self: flex-start;
      cursor: pointer;
      font-size: 20px; }
  .NoticeBox__title {
    background: #ffffff;
    display: flex;
    align-items: center;
    height: 50px;
    border-bottom: 1px solid #ebebeb; }
    .NoticeBox__title__item {
      display: flex;
      justify-content: center;
      display: flex;
      align-items: center;
      height: 100%;
      width: 50%;
      text-align: center;
      color: #001a33;
      font-size: 16px;
      border-bottom: 2px solid #ffffff; }
      .NoticeBox__title__item .box {
        cursor: pointer;
        display: flex;
        align-items: center;
        height: 100%;
        position: relative;
        padding: 0 11px; }
        .NoticeBox__title__item .box .MenuItem {
          font-family: SFProText;
          font-weight: 500;
          font-size: 14px; }
        .NoticeBox__title__item .box .MenuBadge {
          top: -8px; }
    .NoticeBox__title__item.menuItemActive .box .MenuItem {
      font-family: SFBoldText; }
    .NoticeBox__title .titleMsgNum {
      right: 0px;
      top: 4px; }
  .NoticeBox__Content {
    height: 635px;
    background: #ffffff;
    position: relative; }

.menuItemActive {
  color: #3bcdfe;
  border-bottom: 2px solid #3bcdfe;
  font-family: SFBoldText;
  font-weight: 500; }

.hide {
  display: none; }

.fold {
  font-size: 16px; }

.location {
  width: 19px;
  height: 14px;
  position: absolute;
  bottom: 0;
  left: 30px;
  z-index: 2; }

.lastMsgIcon {
  margin-right: 4px;
  font-size: 14px; }

.msgTime {
  font-size: 12px;
  color: #8a9199;
  padding-top: 3px;
  height: 18px;
  margin-left: auto; }

.show {
  display: block; }

.hide {
  display: none; }

.activeOrder {
  background: #ebebeb; }

.noMsg {
  padding: 120px 0 0 0;
  text-align: center; }
  .noMsg .noMsgCon {
    font-size: 14px;
    line-height: 1.5;
    color: #8a9199; }

.mileage_icon {
  font-size: 16px;
  display: flex;
  justify-content: center;
  display: flex;
  align-items: center;
  color: #198cff !important;
  margin-right: 4px;
  padding-bottom: 1px; }

.showMoreOrder {
  display: flex;
  justify-content: center;
  display: flex;
  align-items: center;
  height: 50px;
  font-size: 16px;
  color: #5c6166; }
  .showMoreOrder > span {
