.container {
  display: flex;
  justify-content: center;
  display: flex;
  align-items: center;
  font-family: SFProText;
  color: #c3ced9;
  font-size: 60px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  cursor: pointer;
  width: 408px;
  height: 230px;
  border-radius: 8px;
  border: dashed 2px #c3ced9;
  background-color: #ffffff; }
  .container .add {
    display: flex;
    justify-content: center;
    display: flex;
    align-items: center;
    cursor: pointer;
    position: relative;
    width: 60px;
    height: 60px;
    border: 0; }
    .container .add .col {
      position: absolute;
      width: 4px;
      height: 60px;
      border-radius: 1px;
      background-color: #c3ced9; }
    .container .add .row {
      position: absolute;
      width: 60px;
      height: 4px;
      border-radius: 1px;
      background-color: #c3ced9; }
