@font-face {
  font-family: SFProText;
  src: url("../../../../assets/fonts/sf-pro-text_regular.woff2") format("woff2"); }

@font-face {
  font-family: SFBigText;
  src: url("../../../../assets/fonts/sf-pro-display_regular.woff2") format("woff2"); }

@font-face {
  font-family: SFBoldText;
  src: url("../../../../assets/fonts/sf-pro-text_semibold.woff2") format("woff2"); }

@font-face {
  font-family: DIN;
  src: url("../../../../assets/fonts/dinweb-medium-woff-data.woff") format("woff"); }

.ant-notification {
  width: 298px !important; }

.ant-notification-notice {
  padding: 12px !important; }

.ant-notification-notice-message {
  margin-bottom: 0 !important;
  padding-right: 0px !important; }

.ant-notification-notice-close {
  margin-top: -5px !important;
  top: 6px !important;
  right: 12px !important; }
  .ant-notification-notice-close svg {
    width: 10px !important;
    height: 10px !important;
    color: #001a33 !important; }

.container {
  width: 100%;
  height: 100%;
  background-color: #f6f8f9; }
  .container .content {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: calc(100% - 64px);
    padding: 56px 40px 40px 40px; }
    .container .content .title {
      font-family: SFProText;
      color: #001a33;
      font-size: 14px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.5;
      letter-spacing: normal;
      font-family: SFBoldText;
      color: #001a33;
      font-size: 18px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.5;
      letter-spacing: normal;
      margin-bottom: 12px; }
    .container .content .accountInfo {
      display: flex;
      border-radius: 8px;
      flex-direction: column;
      padding: 40px;
      width: 760px;
      height: 100%;
      overflow-y: scroll;
      background-color: #ffffff; }
      .container .content .accountInfo::-webkit-scrollbar {
        display: none; }
      .container .content .accountInfo .InputItem {
        margin-bottom: 32px; }
        .container .content .accountInfo .InputItem .input {
          margin-bottom: 8px; }
          .container .content .accountInfo .InputItem .input:last-child {
            margin-bottom: 0px; }
  .container .content.view {
    height: 100%; }
  .container button.button {
    display: flex;
    justify-content: center;
    display: flex;
    align-items: center;
    font-family: SFBoldText;
    color: #ffffff;
    font-size: 24px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.5;
    letter-spacing: normal;
    border: 0px;
    border-radius: 0px;
    width: 100%;
    height: 64px;
    background-color: #3acdfe; }
    .container button.button:hover {
      font-family: SFBoldText;
      color: #ffffff;
      font-size: 24px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.5;
      letter-spacing: normal;
      background-color: #3acdfe;
      border: 0px; }
    .container button.button:focus {
      font-family: SFBoldText;
      color: #ffffff;
      font-size: 24px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.5;
      letter-spacing: normal;
      background-color: #3acdfe;
      border: 0px; }

.col {
  display: flex;
  flex-direction: column;
  width: 400px; }
  .col .message {
    font-family: SFBoldText;
    color: #001a33;
    font-size: 16px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.5;
    letter-spacing: normal;
    margin-bottom: 8px; }
  .col .input {
    width: 100%;
    border-radius: 0px;
    border: solid 1px #ebebeb;
    background-color: #ffffff; }
