@font-face {
  font-family: SFProText;
  src: url("../../assets/fonts/sf-pro-text_regular.woff2") format("woff2"); }

@font-face {
  font-family: SFBigText;
  src: url("../../assets/fonts/sf-pro-display_regular.woff2") format("woff2"); }

@font-face {
  font-family: SFBoldText;
  src: url("../../assets/fonts/sf-pro-text_semibold.woff2") format("woff2"); }

@font-face {
  font-family: DIN;
  src: url("../../assets/fonts/dinweb-medium-woff-data.woff") format("woff"); }

.ant-notification {
  width: 298px !important; }

.ant-notification-notice {
  padding: 12px !important; }

.ant-notification-notice-message {
  margin-bottom: 0 !important;
  padding-right: 0px !important; }

.ant-notification-notice-close {
  margin-top: -5px !important;
  top: 6px !important;
  right: 12px !important; }
  .ant-notification-notice-close svg {
    width: 10px !important;
    height: 10px !important;
    color: #001a33 !important; }

.RegisterReviewedBox {
  width: 800px;
  height: 800px;
  background: #ffffff;
  padding: 120px 200px 24px 200px;
  position: relative; }

.ReviewedIcon {
  margin-bottom: 32px; }

.ReviewedMsgBox {
  display: table;
  margin: 0 auto; }
  .ReviewedMsgBox .label {
    padding-left: 40px;
    font-family: SFProText;
    font-size: 16px;
    color: #001a33;
    margin-bottom: 8px; }
  .ReviewedMsgBox .ReviewedMsgStatus .ReviewedMsg__Icon {
    margin-right: 8px; }
  .ReviewedMsgBox .ReviewedMsgStatus .ReviewedMsg__content {
    font-size: 36px;
    font-weight: 600;
    font-family: SFBoldText;
    color: black;
    margin-bottom: 8px; }
  .ReviewedMsgBox .ReviewedHint {
    font-size: 16px;
    font-family: SFProText;
    color: #acb6bf;
    padding-left: 40px; }

.subHint {
  position: absolute;
  bottom: 24px;
  left: 0;
  right: 0;
  text-align: center;
  margin-bottom: 0;
  font-size: 14px; }
  .subHint__label {
    color: #acb6bf; }
  .subHint .contact {
    color: #3acdfe; }
