@font-face {
  font-family: SFProText;
  src: url("../../../../../assets/fonts/sf-pro-text_regular.woff2") format("woff2"); }

@font-face {
  font-family: SFBigText;
  src: url("../../../../../assets/fonts/sf-pro-display_regular.woff2") format("woff2"); }

@font-face {
  font-family: SFBoldText;
  src: url("../../../../../assets/fonts/sf-pro-text_semibold.woff2") format("woff2"); }

@font-face {
  font-family: DIN;
  src: url("../../../../../assets/fonts/dinweb-medium-woff-data.woff") format("woff"); }

.ant-notification {
  width: 298px !important; }

.ant-notification-notice {
  padding: 12px !important; }

.ant-notification-notice-message {
  margin-bottom: 0 !important;
  padding-right: 0px !important; }

.ant-notification-notice-close {
  margin-top: -5px !important;
  top: 6px !important;
  right: 12px !important; }
  .ant-notification-notice-close svg {
    width: 10px !important;
    height: 10px !important;
    color: #001a33 !important; }

.DatePickPri .ant-calendar-column-header {
  height: 36px; }

.DatePickPri .ant-calendar-cell {
  width: 36px;
  padding: 0;
  height: 36px;
  border-right: 1px solid #ebebeb;
  border-bottom: 1px solid #ebebeb; }

.DatePickPri .ant-calendar-date {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  display: flex;
  align-items: center;
  border-radius: 0 !important; }
  .DatePickPri .ant-calendar-date:hover {
    background: #ebebeb; }

.DatePickPri .ant-calendar-today ::before {
  border: none !important;
  background: none !important; }

.DatePickPri .ant-calendar-disabled-cell .ant-calendar-date {
  background: none !important;
  color: #ebebeb; }

.DatePickPri .ant-calendar-selected-day.ant-calendar-selected-date .ant-calendar-date {
  background-color: #3acdfe;
  color: #ffffff; }

.DatePickPri tbody tr:first-child td {
  border-top: 1px solid #ebebeb; }

.DatePickPri tbody tr td:first-child {
  border-left: 1px solid #ebebeb; }

.ant-calendar-selected-day .ant-calendar-date {
  background: none; }
