@font-face {
  font-family: SFProText;
  src: url("../../assets/fonts/sf-pro-text_regular.woff2") format("woff2"); }

@font-face {
  font-family: SFBigText;
  src: url("../../assets/fonts/sf-pro-display_regular.woff2") format("woff2"); }

@font-face {
  font-family: SFBoldText;
  src: url("../../assets/fonts/sf-pro-text_semibold.woff2") format("woff2"); }

@font-face {
  font-family: DIN;
  src: url("../../assets/fonts/dinweb-medium-woff-data.woff") format("woff"); }

.ant-notification {
  width: 298px !important; }

.ant-notification-notice {
  padding: 12px !important; }

.ant-notification-notice-message {
  margin-bottom: 0 !important;
  padding-right: 0px !important; }

.ant-notification-notice-close {
  margin-top: -5px !important;
  top: 6px !important;
  right: 12px !important; }
  .ant-notification-notice-close svg {
    width: 10px !important;
    height: 10px !important;
    color: #001a33 !important; }

.upLoadFormLoading {
  width: 800px;
  height: 800px;
  background: rgba(255, 255, 255, 0.7);
  display: flex;
  justify-content: center;
  display: flex;
  align-items: center;
  position: absolute;
  z-index: 2; }

.upLoadFormBox {
  width: 800px;
  height: 800px;
  background: #ffffff;
  padding: 48px 100px;
  position: relative; }

.formConatiner {
  margin: 0 auto; }
  .formConatiner .formLabel {
    font-size: 16px;
    color: #8a9199;
    line-height: 19px;
    font-family: SFProText;
    margin-bottom: 16px;
    text-align: center; }
  .formConatiner .formTitle {
    position: relative;
    line-height: 29px;
    font-family: SFProText;
    color: #001a33;
    font-size: 24px;
    letter-spacing: -1px;
    margin-bottom: 48px;
    text-align: center; }
    .formConatiner .formTitle::before, .formConatiner .formTitle::after {
      position: absolute;
      display: block;
      content: '';
      width: 100px;
      height: 0;
      border-bottom: 1px solid #ebebeb;
      top: 14px; }
    .formConatiner .formTitle::before {
      left: -4px; }
    .formConatiner .formTitle::after {
      right: -4px; }

.upLoadFormContainer {
  font-size: 0; }
  .upLoadFormContainer .upFormController {
    width: 600px;
    height: 96px;
    display: flex;
    align-items: center;
    padding: 18px 18px 18px 30px;
    border: solid 1px #ebebeb;
    border-radius: 4px;
    margin-bottom: 32px; }
    .upLoadFormContainer .upFormController .upItemIcon {
      margin-right: 30px; }
    .upLoadFormContainer .upFormController .upItemLabel {
      color: #001a33;
      font-family: SFProText;
      font-size: 16px;
      line-height: 24px;
      width: 281px;
      margin-bottom: 0; }
    .upLoadFormContainer .upFormController .uploader {
      width: 60px;
      height: 60px;
      margin-left: auto; }
      .upLoadFormContainer .upFormController .uploader.borderRed :global(.ant-upload.ant-upload-select-picture-card) {
        border: 1px dashed #f2565f; }
      .upLoadFormContainer .upFormController .uploader.borderNone :global(.ant-upload.ant-upload-select-picture-card) {
        border: none; }
      .upLoadFormContainer .upFormController .uploader :global(.ant-upload.ant-upload-select-picture-card) {
        width: 60px;
        height: 60px;
        margin: 0;
        background: #ffffff; }
        .upLoadFormContainer .upFormController .uploader :global(.ant-upload.ant-upload-select-picture-card) :global(span) {
          padding: 0; }
      .upLoadFormContainer .upFormController .uploader .viewImg {
        display: block;
        width: 58px;
        height: 58px;
        object-fit: cover;
        border-radius: 4px; }

.submitBtn {
  width: 100%;
  height: 40px !important;
  border-radius: 4px !important;
  background-color: #3acdfe !important;
  color: #ffffff !important;
  border: none !important;
  font-family: SFBoldText;
  font-size: 16px !important;
  margin-top: 48px; }

.Modal :global(.ant-modal-title) {
  text-align: center; }

.Modal__cont {
  font-family: SFProText;
  font-size: 16px;
  color: #001a33; }

.Modal__footer__btn {
  width: 118px;
  height: 40px !important;
  background-color: #3acdfe !important;
  border-radius: 4px;
  color: #ffffff !important;
  font-family: SFProText;
  font-size: 16px !important; }
