.container {
  display: flex;
  justify-content: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  position: relative;
  width: 165px;
  height: 165px;
  padding: 43px;
  border-radius: 4px;
  border: solid 1px #ebebeb;
  cursor: pointer; }
  .container .uploadDiv {
    display: flex;
    justify-content: center;
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%; }
  .container .padding {
    display: flex;
    justify-content: center;
    display: flex;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #ffffff; }

.container1 {
  display: flex;
  justify-content: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 12px; }
  .container1 .upload {
    display: flex;
    justify-content: center;
    display: flex;
    align-items: center;
    width: 100px;
    height: 100px; }
  .container1 .noImages {
    font-family: SFProText;
    color: #5c6166;
    font-size: 14px;
