.container {
  min-height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column; }

.mainStyle {
  flex: 1;
  min-width: 1200px;
  position: relative;
  width: 100vw;
  padding: 16px calc((100vw - 1200px) * 0.5) 32px calc((100vw - 1200px) * 0.5);
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  box-sizing: border-box;
  background-color: #f6f8f9; }

:global html,
:global body {
  overflow-x: hidden; }
