@font-face {
  font-family: SFProText;
  src: url("../../../assets/fonts/sf-pro-text_regular.woff2") format("woff2"); }

@font-face {
  font-family: SFBigText;
  src: url("../../../assets/fonts/sf-pro-display_regular.woff2") format("woff2"); }

@font-face {
  font-family: SFBoldText;
  src: url("../../../assets/fonts/sf-pro-text_semibold.woff2") format("woff2"); }

@font-face {
  font-family: DIN;
  src: url("../../../assets/fonts/dinweb-medium-woff-data.woff") format("woff"); }

.ant-notification {
  width: 298px !important; }

.ant-notification-notice {
  padding: 12px !important; }

.ant-notification-notice-message {
  margin-bottom: 0 !important;
  padding-right: 0px !important; }

.ant-notification-notice-close {
  margin-top: -5px !important;
  top: 6px !important;
  right: 12px !important; }
  .ant-notification-notice-close svg {
    width: 10px !important;
    height: 10px !important;
    color: #001a33 !important; }

.NotesForm {
  font-family: SFProText; }

.InfoContainer {
  padding-bottom: 40px;
  border-bottom: 1px solid #ebebeb; }
  .InfoContainer .title {
    padding-bottom: 8px; }
  .InfoContainer .subTitle {
    padding-bottom: 8px;
    padding-left: 20px; }
  .InfoContainer .infomation {
    color: #8a9199;
    padding-left: 20px; }
  .InfoContainer .Icon {
    color: #3bcdfe !important;
    margin-right: 4px;
    font-size: 16px; }

.optionContainer {
  padding-left: 20px;
  padding-top: 40px;
  padding-bottom: 16px;
  border-bottom: 1px solid #ebebeb; }
  .optionContainer .title {
    padding-bottom: 8px; }
  .optionContainer .subTitle {
    font-size: 14px;
    color: #8a9199;
    padding-bottom: 40px; }

.inlineForm {
  display: flex;
  flex-direction: row;
  justify-content: flex-start; }
  .inlineForm .cost {
    margin-right: 4px; }
  .inlineForm .unit {
    margin-right: 60px; }
    .inlineForm .unit :global .ant-form-item-label {
      opacity: 0; }

.btnRow {
  padding: 20px 0;
  display: flex;
  justify-content: flex-end; }
