@font-face {
  font-family: SFProText;
  src: url("../../../assets/fonts/sf-pro-text_regular.woff2") format("woff2"); }

@font-face {
  font-family: SFBigText;
  src: url("../../../assets/fonts/sf-pro-display_regular.woff2") format("woff2"); }

@font-face {
  font-family: SFBoldText;
  src: url("../../../assets/fonts/sf-pro-text_semibold.woff2") format("woff2"); }

@font-face {
  font-family: DIN;
  src: url("../../../assets/fonts/dinweb-medium-woff-data.woff") format("woff"); }

.ant-notification {
  width: 298px !important; }

.ant-notification-notice {
  padding: 12px !important; }

.ant-notification-notice-message {
  margin-bottom: 0 !important;
  padding-right: 0px !important; }

.ant-notification-notice-close {
  margin-top: -5px !important;
  top: 6px !important;
  right: 12px !important; }
  .ant-notification-notice-close svg {
    width: 10px !important;
    height: 10px !important;
    color: #001a33 !important; }

.row {
  padding-top: 24px;
  display: flex;
  align-items: stretch;
  position: relative;
  border-bottom: 1px solid #ebebeb; }

.vinField {
  margin-right: 80px !important; }
  .vinField :global .ant-form-item-control-wrapper {
    width: 240px;
    padding-left: 10px; }

.mileageField {
  margin-right: 4px !important; }
  .mileageField :global .ant-form-item-control-wrapper {
    width: 136px;
    padding-left: 10px; }

.rowDelete {
  font-family: SFProText;
  font-size: 16px;
  color: #f5313b;
  display: flex;
  align-items: center;
  height: 100%;
  position: absolute;
  right: 0;
  cursor: pointer; }

.fieldCombo {
  display: flex;
  align-items: center; }

.inlineSelect {
  height: 100% !important;
  margin: 0 !important; }
  .inlineSelect :global {
    align-self: flex-end; }
    .inlineSelect :global .ant-form-item-label {
      opacity: 0; }
    .inlineSelect :global .ant-select {
      width: 100px;
      height: 100%; }

.addFieldBtn {
  width: 200px;
  font-family: SFProText;
  padding: 40px 0;
  color: #3bcdfe;
  cursor: pointer; }
  .addFieldBtn svg {
    width: 16px;
    height: 16px;
    margin-right: 4px; }

.btnRow {
  text-align: right;
  width: 100%; }

.nextBtn :global {
  padding: 10px 62px !important;
  height: 40px !important;
  outline: none; }
