/* 
  公共图标样式
 */
@font-face {
  font-family: SFProText;
  src: url("../fonts/sf-pro-text_regular.woff2") format("woff2"); }

@font-face {
  font-family: SFBigText;
  src: url("../fonts/sf-pro-display_regular.woff2") format("woff2"); }

@font-face {
  font-family: SFBoldText;
  src: url("../fonts/sf-pro-text_semibold.woff2") format("woff2"); }

@font-face {
  font-family: DIN;
  src: url("../fonts/dinweb-medium-woff-data.woff") format("woff"); }

.ant-notification {
  width: 298px !important; }

.ant-notification-notice {
  padding: 12px !important; }

.ant-notification-notice-message {
  margin-bottom: 0 !important;
  padding-right: 0px !important; }

.ant-notification-notice-close {
  margin-top: -5px !important;
  top: 6px !important;
  right: 12px !important; }
  .ant-notification-notice-close svg {
    width: 10px !important;
    height: 10px !important;
    color: #001a33 !important; }

.icon_mileage_area > svg {
  width: 20px;
  height: 20px;
  color: #198cff; }

.icon_fire_area > svg {
  margin-top: -1px;
  width: 16px;
  height: 16px;
  color: #ff9910; }

.icon_comment_line > svg {
  width: 28px;
  height: 28px;
  color: #ffffff; }

.icon_fork_line_round > svg {
  width: 14px;
  height: 14px;
  color: #f2565f; }

.icon_choose_line > svg {
  width: 14px;
  height: 14px;
  color: #21c442; }

.port_line > svg {
  width: 24px;
  height: 24px;
  color: #3bcdfe; }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

.red {
  color: #f2565f; }

.black, .bold {
  color: #001a33; }

.bold {
  font-weight: 500; }

.font-gray {
  color: #8a9199; }

.hr, .hr-solid, .hr-dashed {
  height: 1px;
  width: 100%;
  margin-top: 20px; }

.hr-solid {
  background-color: #ebebeb; }

.hr-dashed {
  background-image: linear-gradient(to right, #ebebeb 50%, #ffffff 0%);
  background-position: bottom;
  background-size: 10px 1px;
  background-repeat: repeat-x; }

.cursor-pointer {
  cursor: pointer; }

.prevent-scroll::-webkit-scrollbar {
  width: 0 !important; }

.ant-pagination-item-active {
  border-color: #3acdfe !important; }
  .ant-pagination-item-active a {
    color: #3acdfe !important; }

.ant-tooltip-inner {
  font-family: SFProText;
  min-height: auto !important;
  background-color: white !important;
  color: black !important;
  padding: 8px 12px !important;
  font-size: 12px !important;
  line-height: 1.5 !important;
  box-sizing: border-box !important; }

.ant-tooltip-arrow:before, .ant-tooltip-arrow:after {
  background-color: #ffffff !important; }

.order .ant-tooltip-content,
.order .ant-tooltip-arrow {
  margin-top: -30px !important; }
